import React from 'react';
import "comps/panel_popup/header/PopupPanelHeader.css" 
import assets_internal from 'assets_internal/assets_internal' 
 
interface IPopupPanelHeaderProps
{
  vvv:string
}

 

class PopupPanelHeader extends React.Component<IPopupPanelHeaderProps>
{
 
  
  render()
  {
    return (
      <div className='CT_PanelHeader'>
        <img className='IMG_PanelHeader' src={assets_internal.panel_header} alt="" />
        <div className="PanelHeaderTitle">{this.props.vvv}</div>
        
      </div>
    );
  } 
}
 

export default PopupPanelHeader;