import KarlSiriusEntity from './KarlSiriusEntity'
import IKarlSiriusView from './IKarlSiriusView'  

export default abstract class KarlSiriusVMBase
{
    protected _entity:KarlSiriusEntity;
    protected _pair_view?:IKarlSiriusView;

    public get entity():KarlSiriusEntity
    {
        return this._entity;
    }
    public local_entity = <T extends KarlSiriusEntity>():T=>
    {
        return this._entity as T;
    }

    constructor()
    {
        this._entity = {};  
        this._pair_view = undefined;
    }
    
    public establish = ():void =>
    {
        this._preinit_entity();
 
    }

    public attach_root = (vv:IKarlSiriusView):void=>
    {
        this._pair_view = vv;
        vv.register_vm(this);
    }



    //============= protected ===========//
    protected _notify_view_update = ():void=>
    {
        this._pair_view?.notify_render(this._entity);
    }

    //============= need override ===========//
    protected _preinit_entity = ():void =>
    {

    } 
}