import React from 'react';
import "comps/input/InputLine.css"  

import KarlSiriusRenderElement from 'sirius/render_element/KarlSiriusRenderElement'
import KarlSiriusRenderElementProp from "sirius/render_element/KarlSiriusRenderElementProp"
import IKarlSiriusRenderElementState from "sirius/render_element/IKarlSiriusRenderElementState"

 
class InputLineProps extends KarlSiriusRenderElementProp
{
  title!:string;
  is_pwd!:boolean;
}

interface IInputLineState extends IKarlSiriusRenderElementState
{
    has_error:boolean;
    error_text:string;
}


class InputLine extends KarlSiriusRenderElement<InputLineProps, IInputLineState>
{
  static defaultProps:InputLineProps = { 
    title: "",
    is_pwd: false
  }
  constructor(props:InputLineProps)
  {
    super(props); 
    this.state = {
  
      has_error:false,
      error_text:""
    }
  }
 
  render()
  {
    return (
      <div className='InputLine'>
        
        <div className='InputLineMain'>
            <div className='InputLineTitle'>{this.props.title}</div>
            <div className='InputTextArea'>
                <div  className='InputTextAreaBG'></div>
                <input className='InputLineText' type={this.props.is_pwd? "password" : "text"} />
            </div> 
        </div>
        <div className='InputLineErrorText' 
          style={{
            display:this.state.has_error? "block" : "none"
            }}>
          {this.state.error_text}
        </div> 
      </div>
    );
  } 
}

 

export default InputLine;