import React from 'react';
import "pages/global/SDKGlobal.css"
import "pages/login/LoginPage.css"

import KarlSiriusViewBase from "sirius/base/KarlSiriusViewBase"
import KarlSiriusRenderElementProp from "sirius/render_element/KarlSiriusRenderElementProp"
import IKarlSiriusRenderElementState from "sirius/render_element/IKarlSiriusRenderElementState"
import SDKCore from 'core/SDKCore'
import LoginPageVM from './LoginPageVM'

import BtnHyperlink from 'comps/btn/Btn_Hyperlink'
import BtnDefault from 'comps/btn/Btn_Default'
import PopupPanel from 'comps/panel_popup/PopupPanel'
import InputLine from 'comps/input/InputLine'


class LoginPageProp extends KarlSiriusRenderElementProp
{
  evt_goto_register!: () => void;
  evt_close!: () => void;
}

interface ILoginPageState extends IKarlSiriusRenderElementState
{
  
}


class LoginPage extends KarlSiriusViewBase<LoginPageProp, ILoginPageState>
{
  constructor(props: LoginPageProp)
  {
    super(props);
    this.state = SDKCore.i.login.entity;
    SDKCore.i.login.attach_root(this);
  }
 
  private goto_register = () =>
  {
    this.props.evt_goto_register();
  }


  //"登录GameSDK"
  // ref={(ref:PopupPanel|null)=>{this._register_child(ref!)}}
  render()
  {
    return (
      <PopupPanel
        panel_title="登录GameSDK"
        panel_left='0%'
        max_height='600px'
        evt_close={this.props.evt_close}
      >

        <div className='Area_MainBtn'>
          <BtnDefault vvv={'登录'} evt_click={this.local_vm<LoginPageVM>().handleLogin}></BtnDefault>
        </div>

        <div className='Area_BottomTip'>
          <div className='CT_RegisterTipText'>还没有账户？</div>
          <BtnHyperlink vvv="注册" evt_click={this.goto_register}></BtnHyperlink>
        </div>

        <div className='Area_MainContent'>
          <InputLine 
            ref={(ref: InputLine | null) => { this._register_child("user_name", ref!, "uuu") }} 
            title='手机号'
          ></InputLine>
          <InputLine 
            ref={(ref: InputLine | null) => { this._register_child("user_pwd", ref!, "ppp") }} 
            title='密码'
            is_pwd={true}
          ></InputLine>
        </div>
      </PopupPanel>

    );
  }
}



export default LoginPage;