import KarlSiriusVMBase from "sirius/base/KarlSiriusVMBase" 
import KarlSiriusEntity from 'sirius/base/KarlSiriusEntity'
import IKarlSiriusRenderElementState from "sirius/render_element/IKarlSiriusRenderElementState";
 
export class GlobalLoadingEntity extends KarlSiriusEntity
{
    is_loading!:boolean;
}

export default class GlobalLoadingVM extends KarlSiriusVMBase
{
    _lc:GlobalLoadingEntity;
    constructor()
    {
        super();
        this._lc = new GlobalLoadingEntity();
        this._entity = this._lc;
    }

    public swap_loading(is_visual:boolean):void
    {
        this._lc.is_loading = is_visual;
        this._notify_view_update();
    }
}
