import SDKCore from "core/SDKCore";

export interface sdk_login_res
{
    uid:string;
}

export interface isdk_login_param
{
    
}

type login_resolve = (value :sdk_login_res | PromiseLike<sdk_login_res>)=>void;
type login_reject = (reason?:any)=>void;
 
export default class sdk_interface_login
{
    private _param?:isdk_login_param;
    private _resolve?:login_resolve;
    private _reject?:login_reject;
    private _clear_promise = ():void=>
    {
        this._resolve = undefined;
        this._reject = undefined;
    }

    public login = (param:isdk_login_param):Promise<sdk_login_res>=>
    { 
        const pp:Promise<sdk_login_res> = new Promise<sdk_login_res>(
            (ss:login_resolve, ff:login_reject)=>
            {
                this._resolve = ss;
                this._reject = ff;
            });
        
            SDKCore.i.sdki._sdki_init.app_show();
        return pp;
    }

    public login_success = (uid:string):void=>
    {
        const res:sdk_login_res = 
        {
            uid:uid
        };
        if(this._resolve)
        {
            this._resolve(res);
            this._clear_promise();
        } 
    }
    public login_error = (reason:any):void=>
    {
        if(this._reject)
        {
            this._reject(reason);
            this._clear_promise();
        }
    } 
}