import React from 'react';
import * as ReactDOMClient from 'react-dom/client';
import App from '../App';
import SDKCore from 'core/SDKCore'

export interface isdk_init_param
{
    app_id?: string;
}


type init_resolve = (value :void | PromiseLike<void>)=>void;
type init_reject = (reason?:any)=>void;

export default class sdk_interface_init
{
    private _sdk_inited: boolean;
    private _param?:isdk_init_param;
    private _resolve?:init_resolve;
    private _reject?:init_reject;

    private root_container!: HTMLElement;

    private _clear_promise = ():void=>
    {
        this._resolve = undefined;
        this._reject = undefined;
    }

    constructor()
    {
        this._sdk_inited = false; 
    }
 
    //=================== init ================//
    
    private _promote_root(): HTMLElement
    {
        let container: HTMLElement | null = document.getElementById('sdk_root');
        if (container == null)
        {
            container = document.createElement("div");
            container.id = 'sdk_root';
            document.body.appendChild(container);
            container.style.width="100vw";
            container.style.height="100vh";
            container.style.left="0px";
            container.style.top="0px";
            container.style.position = 'absolute';
            // container.style.backgroundColor = "brown"
            
        }
        this.root_container = container;
        return container;
    }

    private _view_established = (app_instance: App | null): void =>
    {
        if (app_instance == null)
        {
            console.error("can not establish view tree!");
            return;
        }
        SDKCore.i.view_establish(app_instance);
        if(this._resolve)
        {
            this._resolve();
            this._clear_promise();
        }
    }

    private _establish_root = (): void =>
    {
        const container: HTMLElement = this._promote_root();
        const root: ReactDOMClient.Root = ReactDOMClient.createRoot(container!);
        root.render(
            <React.StrictMode>
                <App ref={appinstance => { this._view_established(appinstance); }} />
            </React.StrictMode>
        );
    }


    public init_sdk = (param:isdk_init_param): Promise<void> =>
    {
        if (this._sdk_inited)
        { 
            return new Promise<void>((ss:init_resolve , ff:init_reject):void=>{
                ss();
            });
        }
        const pp:Promise<void> = new Promise<void>((ss:init_resolve , ff:init_reject):void=>{
            this._resolve = ss;
            this._reject = ff;
        });

        this._param = param;
        this._sdk_inited = true;
        SDKCore.i.core_init();
        this._establish_root();

        return pp;
    }

    public app_show = ():void =>
    {
        this.root_container.style.display = "block";
        SDKCore.i.ui_root?.app_show();
    }
    public app_hide = ():void =>
    {
        this.root_container.style.display = "none";
        SDKCore.i.ui_root?.app_hide();
    }
}