import LoginPageVM from 'pages/login/LoginPageVM'
import App from '../App';
import RegisterPageVM from 'pages/register/RegisterPageVM'
import sdk_log_consts from 'consts/sdk_log_consts'
import GlobalLoadingVM from 'pages/loading/GlobalLoadingVM';
import {SDKInterface} from '../index'

export default class SDKCore
{
    private static _instance: SDKCore;
    public static create_instance(ii:SDKInterface):void
    {
        SDKCore._instance = new SDKCore(ii);
    }

    public static get i()
    { 
        return SDKCore._instance;
    }

    public ui_root?: App;
    public login: LoginPageVM;
    public register: RegisterPageVM;
    public global_loading:GlobalLoadingVM;
    public sdki:SDKInterface;

    constructor(ii:SDKInterface)
    {
        this.sdki = ii;
        this.login = new LoginPageVM();
        this.register = new RegisterPageVM();
        this.global_loading = new GlobalLoadingVM();
    }

    public core_init(): void
    { 
        // console.log(sdk_log_consts.log_prefix + "core_init");
    }

    public view_establish(vroot:App):void
    {
        // console.log(sdk_log_consts.log_prefix + "view_establish");
        this.ui_root = vroot;
    }
}