// import { executionAsyncId } from 'async_hooks';
// import { eventNames } from 'process';
import React from 'react';
import KarlSiriusVMBase from '../base/KarlSiriusVMBase'  
import IKarlSiriusRenderElementState from './IKarlSiriusRenderElementState'  
import KarlSiriusRenderElementProp from './KarlSiriusRenderElementProp'
import IKarlSiriusRenderElement from './IKarlSiriusRenderElement'

export default abstract class  KarlSiriusRenderElement<P extends KarlSiriusRenderElementProp,S extends IKarlSiriusRenderElementState> extends React.Component<P,S> implements IKarlSiriusRenderElement 
{  
    protected _binding_key?:string;
    public set_binding_key(kkk:string):void
    {
        this._binding_key = kkk;
    }

    protected _vm?:KarlSiriusVMBase;
    public set_vm(vmobj: KarlSiriusVMBase): void
    {
        this._vm = vmobj;
    }
    
    protected _parent_ele? : IKarlSiriusRenderElement;
    public set_parent_ele = (p: IKarlSiriusRenderElement): void =>
    {
        this._parent_ele = p;
    }
    protected _children_ele : Map<string,IKarlSiriusRenderElement>;
  
    protected local_vm = <T extends KarlSiriusVMBase>():T=>
    {
        return this._vm as T;   
    }

    protected _local_state?:IKarlSiriusRenderElementState;

    constructor(props:P)
    {
        super(props);
        this._vm = undefined;
        this._parent_ele = undefined;
        this._children_ele = new Map<string,IKarlSiriusRenderElement>();
    }
  



    public notify_render = (ee:IKarlSiriusRenderElementState):void =>
    {
        // console.log(this)
        // console.log(ee)
        this._local_state = this._convert_state(ee);
        this.setState(this._local_state); 
        this._children_ele.forEach((value: IKarlSiriusRenderElement, key: string) => 
        {
            value.notify_render(this._local_state!);
        }); 
    }
 
    //================
  
    protected _register_child = (kkk:string, cc:IKarlSiriusRenderElement, param:any):void =>
    {  
        
        // console.log("--- _register_child");
        if(cc == null)
        {
            // console.log("is null");
            return;
        }  
        cc.set_binding_key(kkk);
        cc.set_parent_ele(this);
        cc.set_vm(this._vm!);
        this._children_ele.set(kkk, cc); 
    }


    protected _convert_state = (cc:IKarlSiriusRenderElementState):IKarlSiriusRenderElementState =>
    {
        if(this._binding_key == undefined)
        {
            return cc;
        }
        return (cc as any)[this._binding_key]; 
    }
}
 
 