import sirius_base_object from "sirius/root/sirius_base_object";

export default class DelayDispatcher extends sirius_base_object
    {

        public ddid: number; 
        protected _delay_time: number;
        protected _target: any;
        protected _fun: any;
        protected _param: any;

        public constructor()
        { 
            super();
            this.ddid = 0;
            this._delay_time = -1;
        }
 
        protected _before_destroy():void
        { 
            this._target = null;
            this._fun = null;
            this._param = null;
        }
  


        public dispatch(time: number, target: any, fun: any, param: any = null)
        {
            this._is_destroyed = false;
            this._delay_time = time;
            this._target = target;
            this._fun = fun;
            this._param = param;

            setTimeout(() =>
            {
                this.execute_fun();
            }, time);
        }

        protected execute_fun(): void
        {
            if (this._is_destroyed)
            {
                return;
            }

            if (this._target && this._fun
            )
            {
                this._fun.apply(this._target, [this._param]);
            }
 
            this.destroy();
        } 
    }