import React from 'react';
import "pages/global/SDKGlobal.css"
import "comps/panel_popup/PopupPanel.css"
import PopupPanelHeader from 'comps/panel_popup/header/PopupPanelHeader'
import assets_internal from 'assets_internal/assets_internal'

export interface IPopupPanelState
{
    hide_close:boolean;
}


interface IPopupPanelProp
{
    panel_title: string; 
    panel_left:string;
    max_height:string;
    evt_close:()=>void; 
}
 
class PopupPanel extends React.Component<IPopupPanelProp, IPopupPanelState>
{
 
    constructor(props:IPopupPanelProp)
    {
        super(props);
        this.state = {
            hide_close:false
        }
    }

  
    render()
    {
        return (
            <div className="PanelRoot" style={{
                left:this.props.panel_left 
                }}>
                <div className='PanelContent'
                    style={{maxHeight:this.props.max_height}} 
                >
                    <div className='RoundCornerBG'>
                        <div className='PanelBG'></div>
                        <PopupPanelHeader vvv={this.props.panel_title}></PopupPanelHeader> 
                    </div> 
                    <img className='PopupPanelCloseBtn' 
                        src={assets_internal.close_icon} 
                        alt="关闭" 
                        onMouseDown={this.props.evt_close}
                        style={{
                            display:this.state.hide_close ? "none" : "block"
                        }} 
                        />
                     
                    {this.props.children}
                </div> 
            </div>
        );
    }
}



export default PopupPanel;

