import React from 'react';
import './index.css';
//=======
import SDKCore from 'core/SDKCore'
//======= interface
import sdk_interface_init, { isdk_init_param } from 'sdk_interface/sdk_interface_init';
import sdk_interface_login, { isdk_login_param, sdk_login_res } from 'sdk_interface/sdk_interface_login';



export class SDKInterface
{
  public _sdki_init: sdk_interface_init;
  public _sdki_login: sdk_interface_login;

  public init:(param:isdk_init_param)=>Promise<void>;
  public login:(param:isdk_login_param)=>Promise<sdk_login_res>;

  constructor()
  {
    SDKCore.create_instance(this);
    
    this._sdki_init = new sdk_interface_init();
    this._sdki_login = new sdk_interface_login();
    
    this.init = this._sdki_init.init_sdk;
    this.login = this._sdki_login.login;
  } 
}


declare global 
{
  interface Window 
  {
    sirius: SDKInterface;
  }
}

window.sirius = new SDKInterface();



