import React from 'react';
import "pages/register/RegisterPage.css"

import KarlSiriusViewBase from "sirius/base/KarlSiriusViewBase"
import KarlSiriusRenderElementProp from "sirius/render_element/KarlSiriusRenderElementProp"
import IKarlSiriusRenderElementState from "sirius/render_element/IKarlSiriusRenderElementState"
import SDKCore from 'core/SDKCore'
import RegisterPageVM  from './RegisterPageVM'

import BtnHyperlink from 'comps/btn/Btn_Hyperlink'
import BtnDefault from 'comps/btn/Btn_Default'
import PopupPanel from 'comps/panel_popup/PopupPanel'
import InputLine from 'comps/input/InputLine'

class RegisterPageProp extends KarlSiriusRenderElementProp
{
  evt_goto_login!: () => void;
  evt_close!:()=>void;
}

interface IRegisterPageState extends IKarlSiriusRenderElementState
{
  hide_close?:boolean;
}


class RegisterPage extends KarlSiriusViewBase<RegisterPageProp, IRegisterPageState>
{

  constructor(props:RegisterPageProp)
  {
    super(props);
    this.state = SDKCore.i.register.entity;
    SDKCore.i.register.attach_root(this);
  }
  private handle_register = ()=>
  {

  }

  render()
  {
    return (
      <PopupPanel 
        panel_title='注册为新用户'
        panel_left='50%'
        max_height='900px'
        evt_close={this.props.evt_close}
      >
        <div className='Area_MainBtn'>
          <BtnDefault vvv={'注册'} evt_click={this.handle_register}></BtnDefault>
        </div>
        <div className='Area_BottomTip'>
          <div className='CT_RegisterTipText'>已有账户？</div>
          <BtnHyperlink vvv="去登录" evt_click={this.props.evt_goto_login}></BtnHyperlink>
        </div>

        <div className='Area_MainContent'>
          <InputLine  
              ref={(ref: InputLine | null) => { this._register_child("user_name", ref!, "uuu") }} 
            title='手机号'></InputLine> 
          <InputLine  
          ref={(ref: InputLine | null) => { this._register_child("verify_code", ref!, "uuu") }} 
          title='验证码'
          
          ></InputLine> 
          <InputLine  
          ref={(ref: InputLine | null) => { this._register_child("user_pwd", ref!, "uuu") }} 
          title='密码' is_pwd={true}></InputLine> 
        </div>
      </PopupPanel>

    );
  }
}




export default RegisterPage;