import React from 'react';

import KarlSiriusViewBase from "sirius/base/KarlSiriusViewBase"
import KarlSiriusRenderElementProp from "sirius/render_element/KarlSiriusRenderElementProp"
import IKarlSiriusRenderElementState from "sirius/render_element/IKarlSiriusRenderElementState"
import SDKCore from 'core/SDKCore'

import './GlobalLoadingMask.css' 
import assets_internal from 'assets_internal/assets_internal'

class GlobalLoadingMaskProp extends KarlSiriusRenderElementProp
{

}
interface IGlobalLoadingMaskState extends IKarlSiriusRenderElementState
{
  is_loading?:boolean;
}


export default class GlobalLoadingMask extends KarlSiriusViewBase<GlobalLoadingMaskProp, IGlobalLoadingMaskState>
{

  constructor(prop:GlobalLoadingMaskProp)
  {
    super(prop);
    this.state = SDKCore.i.global_loading.entity;
    SDKCore.i.global_loading.attach_root(this);
  }
 
    componentDidMount = ()=>
    {
        // console.log("---> componentDidMount")
    }

    componentWillUnmount = ()=>
    {
        // console.log("---> componentWillUnmount")
    }
  
  render()
  {
    return (
      <div className='Loading_BG' style={{display:this.state.is_loading ? "flex" : "none"}}> 
        {/* <div className="Loading_Icon"></div>  */}
        <img className='Loading_Icon' src={assets_internal.loading}></img>
      </div>
    );
  } 
}
  