import KarlSiriusVMBase from "sirius/base/KarlSiriusVMBase" 
import KarlSiriusEntity from 'sirius/base/KarlSiriusEntity'
import IKarlSiriusRenderElementState from "sirius/render_element/IKarlSiriusRenderElementState";
import common_req from "network/common/common_req";
import SDKCore from "core/SDKCore";
import DelayDispatcher from "network/common/DelayDispatcher";


export class LoginPageState extends KarlSiriusEntity
{  
  dd:string;
  user_name:IKarlSiriusRenderElementState;
  user_pwd:IKarlSiriusRenderElementState;

  constructor()
  {
    super(); 
    this.dd ="xxxx";
    this.user_name = 
    {
      has_error:true,
      error_text:"t1"
    };
    this.user_pwd = 
    {
      has_error:true,
      error_text:"t2"
    };
  }
}

export default class LoginPageVM extends KarlSiriusVMBase
{
    _le:LoginPageState;
    constructor()
    {
        super();
        this._le = new LoginPageState(); 
        this._entity = this._le;
    }

    public handleLogin = ():void =>
    { 
      // this._le.dd = "yyyy";
      // this._notify_view_update();

      // const req:common_req = new common_req();
      // req.post("abc");

       
      SDKCore.i.global_loading.swap_loading(true);
      const dd:DelayDispatcher = new DelayDispatcher();
      dd.dispatch(3000, this, ()=>{
        SDKCore.i.global_loading.swap_loading(false);
        SDKCore.i.sdki._sdki_login.login_success("1122");
      });


    }
}