import React from 'react';
import './App.css';
import LoginPage from 'pages/login/LoginPage'
import RegisterPage from 'pages/register/RegisterPage'
import GlobalLoadingMask from 'pages/loading/GlobalLoadingMask'
import SDKCore from 'core/SDKCore';

interface IGameSDKAppProp
{
}

interface IGameSDKAppState
{
  wrapper_offset: string;
  is_visible: boolean
}

class App extends React.Component<IGameSDKAppProp, IGameSDKAppState>
{

  constructor(props: IGameSDKAppProp)
  {
    super(props);
    this.state = {
      wrapper_offset: "0%",
      is_visible: false
    };
  }

  //======== public ======//
  public app_show()
  {
    this.setState({
      is_visible: true,
      wrapper_offset: "0%"
    });
  }

  public app_hide = () =>
  {
    this.setState({
      is_visible: false
    });
  }

 

  //======== private ======//
  private _goto_register = () =>
  {
    this.setState({
      wrapper_offset: "-100%"
    });
  }

  private _goto_login = () =>
  {
    this.setState({
      wrapper_offset: "0%"
    });
  }

  private handle_close = ()=>
  {
    SDKCore.i.sdki._sdki_init.app_hide();
  }


  render()
  {
    return (
      <div className="App" style={{ display: this.state.is_visible ? "flex" : "none" }}>

        <div className='AppDarkMask'></div>
        {/* 原本是将closebtn放在CT_PanelRoot中，
        CT_PanelContent专门用作内容的容器 */}
        <div className='CT_PanelRoot'>
          <div className='CT_PanelContent'>
            <div className='LoginAndRegisterWrapper' style={{
              left: this.state.wrapper_offset
            }}>
              <LoginPage 
                evt_goto_register={this._goto_register}
                evt_close={this.handle_close}
              ></LoginPage>
              <RegisterPage  
                evt_goto_login={this._goto_login}
                evt_close={this.handle_close}
              ></RegisterPage>
            </div> 
          </div>
 
        </div>
        
        <GlobalLoadingMask></GlobalLoadingMask>
      </div>
    );
  }
}


export default App;
