import React from 'react';
import "comps/btn/Btn_Hyperlink.css"
 

interface IBtn_HyperlinkProps
{
  vvv:string;
  evt_click: () => void;

}

interface IIBtn_HyperlinkState
{
  textclr:string;
}


class Btn_Hyperlink extends React.Component<IBtn_HyperlinkProps, IIBtn_HyperlinkState>
{

  constructor(props:IBtn_HyperlinkProps)
  {
    super(props);
    this.state = {
        textclr:"#2e2e2e"
    }
  }

  private handleBgHover = (e:any)=>
  { 
    this.setState({
        textclr:"#ff22a9"
    }); 
  }
  private handleBgLeave = (e:any)=>
  { 
    this.setState({
        textclr:"#2e2e2e"
    }); 
  }
  private handleMouseClick = (e:any) =>
  {  
    this.props.evt_click();
  }

  

  render()
  {
    return (
      <div className="Btn_Hyperlink" 
          onMouseDown={this.handleMouseClick}
          onMouseEnter={this.handleBgHover}
          onMouseLeave={this.handleBgLeave}
          > 
        <div className='Btn_Hyperlink_Txt' style={{color:this.state.textclr}}>
          {this.props.vvv}
        </div>
      </div>
    );
  } 
}
 

export default Btn_Hyperlink;