import React from 'react';
import "comps/btn/Btn_Default.css"
 

interface IBtn_DefaultProps
{
  vvv:string;
  evt_click: () => void;
}

interface IIBtn_DefaultState
{
  bgclr:string;
}

class Btn_Default extends React.Component<IBtn_DefaultProps, IIBtn_DefaultState>
{

  constructor(props:IBtn_DefaultProps)
  {
    super(props);
    this.state = {
      bgclr:"#169fcc"
    }
  }

  handleBgHover = (e:any)=>
  { 
    this.setState({
      bgclr:"#10d5ff"
    }); 
  }
  handleBgLeave = (e:any)=>
  { 
    this.setState({
      bgclr:"#169fcc"
    }); 
  }

  

  render()
  {
    return (
      <div className="Btn_Default" 
          onMouseDown={this.props.evt_click}
          onMouseEnter={this.handleBgHover}
          onMouseLeave={this.handleBgLeave}
          >
        <div className='Btn_Default_Bg' 
          style={{backgroundColor:this.state.bgclr}}></div> 
        <div className='Btn_Default_Txt'>
          {this.props.vvv}
        </div>
      </div>
    );
  } 
}
 

export default Btn_Default;